.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }
  
  /* Small Screen */
  @media (min-width:768px){
    .container{
      width: 750px;
    }
  }
  
  /* Meduim Screen */
  @media (min-width:992px){
    .container{
      width: 970px;
    }
  }
  
  /* Large Screen */
  @media (min-width:1200px){
    .container{
      width: 1170px;
    }
  }
  